}

.reveal {
  clip-path: inset(0 100% 0 0);
  transition: clip-path 0.5s;
  transition-timing-function: cubic-bezier(0.85, 0, 0.15, 1);
  overflow: hidden;
}

.animate-revealed {
  clip-path: inset(0);
}

a:hover,
a:focus {
  text-decoration: none;
  color: inherit;
}

b {
